body {
  margin: 0;
}

.search-form {
  text-align: center;
  height: 55px;
  background-color: #5752e0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  height: 30px;
  width: 300px;
  border: 0;
  border-radius: 5px 0 0 5px;
  padding-left: 10px;
}

.search-input:focus {
  outline: none;
}

.icon {
  fill: #000;
  width: 24px;
  height: 24px;
  cursor: pointer;
  padding-left: 4px;
  padding-right: 4px;
}

.search-button {
  height: 32px;
  border: 0;
  border-radius: 0 5px 5px 0;
}

.gallery {
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-top: 1rem;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  margin-right: 0;
  padding: 10px;
  display: flex;
}

.info-item {
  margin: 0;
  padding: 5px 0;
  font-size: 12px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #5752e0;
  border: none;
  border-radius: 5px;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 10px 20px;
  display: none;
}

.load-more:hover {
  box-shadow: 0 2px 4px rgba(0, 0, 0, .5);
}

.btn-container {
  justify-content: center;
  display: flex;
}

.end-message {
  color: red;
  margin-left: 15px;
  font-size: 15px;
  display: none;
}

